body{
  margin: 0;
  background-color: #333333;
}
.container{
  display: grid;
  grid-template-columns: 1fr 100px 1fr;
  align-items: center;
  height: 100vh;
  box-sizing: border-box;
  padding: 20px;
  gap: 20px;
}
.large-area{
  height: 100%;
  padding: 20px;
  box-sizing: border-box;
  color: #eeeeee;
  border: none;
  background-color: #444444;
  border-radius: 10px;
  outline: none;
  resize: none;
  font-family: monospace;
  transition: background-color 0.25s, color 0.25s;
}

