.control__button{
  padding: 8px 14px;
  width: 100%;
  margin-bottom: 10px;
  border-radius: 5px;
  border: none;
  outline: none;
  cursor: pointer;
  background-color: #009578;
  color: #ffffff;
  font-weight: bold;
}

.control__button:active{
  background-color: #00705a;
}